<template>
    <div class="page">
        <breadCrumb :data="navData"></breadCrumb>
        <div class="containner">
            <div class="session">
                <div class="navTitle">
                    <span class="title">基本信息</span>
                    <i @click="editMessage=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    <div class="item">
                        <img src="@/assets/image/logo/logo80.png" alt="">
                        <span class="description" v-if="editMessage==false">
                            <div>实验名称：{{}}</div>
                            <div>实验状态：{{}}</div>
                            <div>解锁阙值：{{}}</div>
                        </span>
                        <span class="description" v-if="editMessage==true">
                            123
                        </span>
                    </div>
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验详情</span>
                    <i @click="editDetail=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    123
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验附件</span>
                    <i @click="editFile=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    123
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验答案</span>
                    <i @click="editAnswer=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    123
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验答案附件</span>
                    <i @click="editAnswerFile=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    123
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验操作</span>
                    <i @click="editOperate=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    123
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import breadCrumb from '@/components/breadCrumb'
export default {
    props: {

    },
    data() {
        return {
            navData:[
                {
                    name: "实验列表",
                    path: "/admin/courseList"
                },{
                    name: "实验详情",
                    path: ""
                }
            ],
            editMessage: false,
            editDetail: false,
            editFile: false,
            editAnswer: false,
            editAnswerFile: false,
            editOperate: false,
        };
    },
    computed: {

    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {

    },
    components: {
        breadCrumb
    },
};
</script>

<style scoped lang="scss">
.page{
    padding: 1em;
    .containner{
        padding:0 1em;
        .session{
            .navTitle{
                padding:10px;
                position: relative;
                border-bottom: 1px solid #E9E9E9;
                height: 1em;
                .title{
                    float: left;
                }
                i{
                    float: right;
                }
            }
            .content{
                padding: 1em;
                .item{
                    .description{
                        display: inline-block;
                        line-height: 2em;
                        margin-left: 1em;
                    }
                }
            }

        }
    }
 
}
</style>



